body {
  font-family: roboto;
}

.main-container {
  width: 850px;
  align-self: center;
  align-items: left;
  flex-direction: column;
  margin: 0 auto;
  display: flex;
}

.loader-container {
  height: 35px;
  margin-left: 20px;
}

.loader {
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 3px solid #96cee0;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  width: 200px;
  border: 1px solid #357185;
  margin-left: 20px;
}

.cat-container {
  gap: 10px;
  padding: 20px;
  display: flex;
}

.cat-image {
  width: 400px;
}

.cat-info {
  width: 400px;
  flex-direction: column;
  display: flex;
}

.temperament-span {
  color: #357185;
  font-weight: 700;
}

.cat-breed {
  color: #357185;
  margin: 0;
}

